'use client'

import { createContext, useContext, useState, ReactNode, useEffect } from 'react'

export interface User {
  id: string
  email: string
  name: string
  phone?: string
  address?: string
  profilePic?: string
}

interface AuthContextType {
  user: User | null
  isLoading: boolean
  login: (email: string, password: string) => Promise<void>
  signup: (email: string, name: string, password: string) => Promise<void>
  logout: () => void
  updateProfile: (updates: Partial<User>) => Promise<void>
}

const AuthContext = createContext<AuthContextType | undefined>(undefined)

export function AuthProvider({ children }: { children: ReactNode }) {
  const [user, setUser] = useState<User | null>(null)
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    const loadUser = async () => {
      const saved = localStorage.getItem('user')
      if (saved) {
        try {
          const parsedUser = JSON.parse(saved)
          setUser(parsedUser)
          
          // Fetch latest profile from database
          try {
            const response = await fetch('/api/auth/profile', {
              headers: {
                'x-user-id': parsedUser.id,
              },
            })
            if (response.ok) {
              const data = await response.json()
              if (data.user) {
                setUser(data.user)
                localStorage.setItem('user', JSON.stringify(data.user))
              }
            }
          } catch (error) {
            console.log('Could not fetch profile from DB, using cached:', error)
          }
        } catch (error) {
          console.log('[v0] Error parsing user from localStorage:', error)
          localStorage.removeItem('user')
        }
      }
      setIsLoading(false)
    }
    loadUser()
  }, [])

  const login = async (email: string, password: string) => {
    setIsLoading(true)
    try {
      const response = await fetch('/api/auth/login', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ email, password }),
      })

      // Check if response is ok before parsing JSON
      if (!response.ok) {
        let errorMessage = 'Login failed'
        let errorDetails = ''
        try {
          const errorData = await response.json()
          errorMessage = errorData.error || errorMessage
          errorDetails = errorData.details || ''
        } catch {
          // If response is not JSON, use status text
          errorMessage = response.statusText || errorMessage
        }
        
        // If it's a database connection error, provide helpful message
        if (errorMessage.includes('Database connection') || errorMessage.includes('whitelist') || errorMessage.includes('IP')) {
          errorMessage = 'Database connection failed. Please check MongoDB Atlas IP whitelist settings. See QUICK_IP_WHITELIST.md for instructions.'
        }
        
        const fullError = errorDetails ? `${errorMessage}\n\n${errorDetails}` : errorMessage
        throw new Error(fullError)
      }

      const data = await response.json()

      if (!data.user) {
        throw new Error('Invalid response from server')
      }

      setUser(data.user)
      localStorage.setItem('user', JSON.stringify(data.user))
    } catch (error: any) {
      console.error('Login error:', error)
      throw error
    } finally {
      setIsLoading(false)
    }
  }

  const signup = async (email: string, name: string, password: string) => {
    setIsLoading(true)
    try {
      const response = await fetch('/api/auth/signup', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ email, name, password }),
      })

      // Check if response is ok before parsing JSON
      if (!response.ok) {
        let errorMessage = 'Signup failed'
        let errorDetails = ''
        try {
          const errorData = await response.json()
          errorMessage = errorData.error || errorMessage
          errorDetails = errorData.details || ''
        } catch {
          // If response is not JSON, use status text
          errorMessage = response.statusText || errorMessage
        }
        
        // If it's a database connection error, provide helpful message
        if (errorMessage.includes('Database connection') || errorMessage.includes('whitelist') || errorMessage.includes('IP')) {
          errorMessage = 'Database connection failed. Please check MongoDB Atlas IP whitelist settings. See QUICK_IP_WHITELIST.md for instructions.'
        }
        
        const fullError = errorDetails ? `${errorMessage}\n\n${errorDetails}` : errorMessage
        throw new Error(fullError)
      }

      const data = await response.json()

      if (!data.user) {
        throw new Error('Invalid response from server')
      }

      setUser(data.user)
      localStorage.setItem('user', JSON.stringify(data.user))
    } catch (error: any) {
      console.error('Signup error:', error)
      throw error
    } finally {
      setIsLoading(false)
    }
  }

  const updateProfile = async (updates: Partial<User>) => {
    if (user) {
      try {
        const response = await fetch('/api/auth/profile', {
          method: 'PUT',
          headers: {
            'Content-Type': 'application/json',
            'x-user-id': user.id,
          },
          body: JSON.stringify(updates),
        })

        if (response.ok) {
          const data = await response.json()
          if (data.user) {
            setUser(data.user)
            localStorage.setItem('user', JSON.stringify(data.user))
          }
        } else {
          // Fallback to local update if API fails
          const updatedUser = { ...user, ...updates }
          setUser(updatedUser)
          localStorage.setItem('user', JSON.stringify(updatedUser))
        }
      } catch (error) {
        // Fallback to local update if API fails
        const updatedUser = { ...user, ...updates }
        setUser(updatedUser)
        localStorage.setItem('user', JSON.stringify(updatedUser))
      }
    }
  }

  const logout = () => {
    setUser(null)
    localStorage.removeItem('user')
  }

  return (
    <AuthContext.Provider value={{ user, isLoading, login, signup, logout, updateProfile }}>
      {children}
    </AuthContext.Provider>
  )
}

export function useAuth() {
  const context = useContext(AuthContext)
  if (!context) throw new Error('useAuth must be used within AuthProvider')
  return context
}
